
.about-wrapper{transition:all 500ms ease 500ms;background:linear-gradient(to left, rgba(255,81,47,0.8), rgba(221,36,118,0.8));display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:end;align-items:flex-end;opacity:0;padding:0 20px;pointer-events:none;position:fixed;top:0;left:0;width:100vw;height:100vh;z-index:999
}
.about-wrapper .about-container{transition:all 500ms ease;transform:translate3d(0, 75%, 0);background:#fff;border-top-left-radius:6px;border-top-right-radius:6px;color:#666;font-size:0.9rem;max-width:800px;opacity:0;padding:20px
}
.about-wrapper .about-container p{margin-bottom:25px
}
.about-wrapper .about-container a{transition:color 250ms linear;color:#DD2476
}
.about-wrapper .about-container a:hover{color:#FF512F
}
.about-wrapper.active{transition:all 500ms ease;pointer-events:auto;opacity:1
}
.about-wrapper.active .about-container{transition:all 500ms ease 500ms;transform:translate3d(0, 0, 0);opacity:1
}



